"use client";

import React, { useState } from "react";
import axios from "axios";
import { Loader2 } from "lucide-react";
import { Button } from "@/components/ui/button";
import { Card, CardContent } from "@/components/ui/card";
import { Input } from "@/components/ui/input";

const API_BASE_URL = "http://127.0.0.1:8000/api/qa/query";

export default function AskQuestionBox() {
  const [documentId, setDocumentId] = useState("");
  const [question, setQuestion] = useState("");
  const [response, setResponse] = useState<any>(null);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState("");

  const handleAsk = async () => {
    if (!documentId || !question) {
      setError("Please provide both Document ID and Question");
      return;
    }

    setLoading(true);
    setError("");
    setResponse(null);

    try {
      const res = await axios.post(
        `${API_BASE_URL}?document_id=${documentId}&question=${encodeURIComponent(question)}`,
        {},
        { headers: { accept: "application/json" } }
      );
      setResponse(res.data);
    } catch (err: any) {
      console.error(err);
      setError(err.response?.data?.detail || "Failed to fetch response");
    } finally {
      setLoading(false);
    }
  };

  return (
    <Card className="w-full max-w-2xl mx-auto mt-6 p-6 shadow-md border border-gray-200">
      <h2 className="text-xl font-semibold mb-4 text-center text-gray-800">
        Ask a Question About Your Document
      </h2>

      <CardContent className="space-y-4">
        <div>
          <label className="text-sm text-gray-700">Document ID:</label>
          <Input
            placeholder="Enter document_id (UUID)"
            value={documentId}
            onChange={(e) => setDocumentId(e.target.value)}
          />
        </div>

        <div>
          <label className="text-sm text-gray-700">Your Question:</label>
          <Input
            placeholder="Type your question here..."
            value={question}
            onChange={(e) => setQuestion(e.target.value)}
          />
        </div>

        <Button
          onClick={handleAsk}
          className="w-full bg-blue-600 hover:bg-blue-700 text-white"
          disabled={loading}
        >
          {loading ? <Loader2 className="animate-spin mr-2" /> : "Ask Question"}
        </Button>

        {error && <p className="text-red-500 text-sm text-center">{error}</p>}

        {/* âœ… Show the full JSON response */}
        {response && (
          <div className="mt-6 bg-gray-50 p-4 rounded-md border border-gray-300 text-sm">
            <h3 className="font-semibold text-gray-800 mb-2">Response:</h3>
            <pre className="whitespace-pre-wrap text-gray-700">
              {JSON.stringify(response, null, 2)}
            </pre>
          </div>
        )}
      </CardContent>
    </Card>
  );
}
